// –í—ã—á–∏—Å–ª–∏—Ç—å —Ä–∞–∑–º–µ—Ä –∫–æ–ª–ª—Å—Ç—ç–∫–∞ –≤ –æ—Å–Ω–æ–≤–Ω—ã—Ö –±—Ä–∞—É–∑–µ—Ä–∞—Ö: Chrome, Firefox, Opera –∏ Safari (–µ—Å–ª–∏ –µ—Å—Ç—å –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å).

export function measureStackSize() {
  let stackSize = 0;

  function recurse() {
    stackSize++;
    recurse();
  }

  try {
    recurse();
  } catch (error) {
    console.log("–ú–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π —Ä–∞–∑–º–µ—Ä —Å—Ç–µ–∫–∞ –≤—ã–∑–æ–≤–æ–≤:", stackSize);
  }
}


