export function countDocumentWrites() {
  let count = 0;

  function tryDocumentWrite() {
    try {
      document.write(`–ü–æ–ø—ã—Ç–∫–∞ –Ω–æ–º–µ—Ä ${count + 1}<br>`);
      count++;
      tryDocumentWrite();
    } catch (error) {
      console.log(`–ú–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—ã–∑–æ–≤–æ–≤ document.write(): ${count}`);
    }
  }

  tryDocumentWrite();
}

